import jsonp from "jsonp-promise";
// jsonp promise for cross site scripting
// https://www.npmjs.com/package/jsonp-promise

// get client ip
// https://stackoverflow.com/questions/391979/how-to-get-clients-ip-address-using-javascript

// get client timezone data zpi
// https://ip-api.io/

// IP Geolocation - JSON endpoint
// using ip-api.com
//https://ip-api.com/docs/api:json

export default async function getUserLocationData() {
  // https://stackoverflow.com/questions/54163952/async-await-in-fetch-how-to-handle-errors

  // grab user data using await
  const response = await jsonp("https://ip-api.com/json/").promise;
  // console.log(response);

  return {
    countryCode: response.countryCode,
    timezone: response.timezone,
  };
}
