import React, { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";

import "./Textarea.scss";

function countWords(str) {
  var str = str.replace(/\S+/g, "+1");
  // TODO: fix this potential loophole
  return eval(str);
}

function Textarea(props) {
  const [wordCount, setWordCount] = useState(0);
  const textareaHeight = useRef(300);

  const textareaRef = useRef();

  const textAreaChange = (e) => {
    const count = countWords(e.target.value) | 0;
    setWordCount(count);

    if (props.onChange) props.onChange(e.target.value);
  };

  const textKeyDown = (e) => {
    // so that the user can change lines without submitting
    if (e.keyCode === 13 && !e.shiftKey) e.stopPropagation();
  };

  useEffect(() => {
    if (props.focus) textareaRef.current.focus();
  }, [textareaRef]);

  return (
    <div className="textarea">
      <textarea
        className="textarea__text"
        onChange={textAreaChange}
        onKeyDown={textKeyDown}
        style={{ height: textareaHeight.current + "px" }}
        placeholder={props.placeholder}
        ref={textareaRef}
      >
        {props.value}
      </textarea>
      <div className="textarea__word-count">
        {wordCount > 1 ? `${wordCount} words` : `${wordCount} word`}
      </div>
    </div>
  );
}

Textarea.propTypes = {
  value: PropTypes.string,
  placeholder: PropTypes.string,
  focus: PropTypes.any,
};

export default Textarea;
