import React, { useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";

function Textfield(props) {
  const textboxRef = useRef(null);
  const [errorMessage, setErrorMessage] = useState(props.errorMessage);
  // i do not know how this line fix all the error message propagating problems but it workd uwu
  const isError = errorMessage;

  useEffect(() => {
    // to select the textbox element when it first show
    if (props.focus) textboxRef.current.select();
  }, []);

  const renderRequired = () => {
    return props.required ? "*Required" : "";
  };

  const onTextChange = (e) => {
    props.onChange(e.target.value);
    validateInput(e.target.value);
  };

  const validateInput = (val) => {
    // do type check everytime user change something
    if (typeof props.validateInput === "function")
      setErrorMessage(props.validateInput(val));
  };

  // pass the validate function trigger
  // for cases when consumer of this component update the value programmatically
  if (props.triggerValidateFunctionRef)
    props.triggerValidateFunctionRef.current = validateInput;

  return (
    <div className={isError ? "field error" : "field"}>
      <label className={isError ? "error" : ""}>{props.label}</label>
      <input
        // {...props}
        className={isError ? "error" : ""}
        placeholder={props.placeholder}
        type={props.type || "text"}
        onChange={onTextChange}
        onClick={(e) => {
          e.target.select();
        }}
        onBlur={props.onBlur}
        onFocus={props.onFocus}
        value={props.value}
        ref={textboxRef}
        autoComplete="on"
      />
      <span className={isError ? "field__assist error" : "field__assist"}>
        {isError ? errorMessage : renderRequired()}
      </span>
    </div>
  );
}

Textfield.propTypes = {
  label: PropTypes.string,
  type: PropTypes.string,
  errorMessage: PropTypes.string,
  placeholder: PropTypes.string,
  required: PropTypes.bool,
  onChange: PropTypes.func,
  // returns false or nothing for valid, return string for error message
  validateInput: PropTypes.func,

  // a reference to get if the props status valid
  triggerValidateFunctionRef: PropTypes.object,
};

export default Textfield;
