export default {
  emptyCheck: (val) => {
    return val !== "";
  },
  emailFormatCheck: (email) => {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  },
  phoneNumberCheck: (val) => {
    const re = /^[+]?[0-9, ]*[(]?[0-9, ]*[)]?[0-9, ]*$/;
    return re.test(val);
  },
};
