import { rawTimeZones } from "@vvo/tzdb";

export default function useTimezone() {
  const list = rawTimeZones.map((val) => {
    return val.rawFormat;
  });
  // const lookup = (countryCode) =>
  //   TimezoneData.filter((value) => {
  //     return value.code === countryCode;
  //   })[0];

  const lookup = (timezoneName) =>
    rawTimeZones.find((val) => {
      return val.name === timezoneName;
    });

  return [list, lookup];
}
