// react wrapper of the caseit style button

import React from "react";
import PropTypes from "prop-types";

function Button(props) {
  let buttonStyle = "";

  // make the button stye default primary
  if (props.type === "primary" || props.type === undefined)
    buttonStyle = "button-primary";
  if (props.type === "secondary") buttonStyle = "button-secondary";

  // the it's a primary button and daibled
  if (buttonStyle === "button-primary" && props.disabled === true)
    buttonStyle += " button-primary--disabled";

  return (
    <button
      className={buttonStyle}
      {...props}
      onClickCapture={(e) => {
        if (props.disabled) e.preventDefault();
      }}
    >
      {props.label}
    </button>
  );
}

Button.propTypes = {
  label: PropTypes.string,
  type: PropTypes.string, // "primary" or "secondary"
  disabled: PropTypes.bool,
};

export default Button;
