import React, { useState, useEffect } from "react";
import { rawTimeZones } from "@vvo/tzdb";
import "./App.scss";

// import components
import Modal from "./components/modal";
import Textfield from "./components/textfield";
import AutocompleteField from "./components/autocompletefield";
import Button from "./components/button";
import Textarea from "./components/textarea";
import Page from "./components/page";
import StepIndicator from "./components/stepIndicator";

import UniversityList from "./data/world-university-names.json";

import FormValidation from "./FormValidation";
import getUserLocationData from "./getUserLocationData";
import useTimezone from "./useTimezone";

//3rd party imports
import { BrowserRouter, Route, Link } from "react-router-dom";
import useStateWithPromise from "./useStateWithPromise";

const NETLIFY_FORM_NAME = "university-application-form";
const REGISTATION_FORM_BASE_URL = "/registration-form";
const FormState = {
  FILLING: 0,
  SUBMITTING: 1,
  SUBMITTED: 2,
  ERROR: 3,
};

function App() {
  // setting up the step indicator
  const TOTAL_STEPS = 8;
  let currentStepCount = 0;
  const countStepAndIncrement = () => {
    currentStepCount++;
    return currentStepCount;
  };

  const [userLocationData, setUserLocationData] = useStateWithPromise();

  const [university, setUniversity] = useState("");
  const [timezone, setTimezone] = useState("");
  const [reason, setReason] = useState("");
  const [unique, setUnique] = useState("");
  const [name, setName] = useState("");
  const [position, setPosition] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [formState, setFormState] = useState(FormState.FILLING);

  const [timezones, lookupTimezone] = useTimezone();

  const isUniversityFormValid = () => {
    return FormValidation.emptyCheck(university);
  };

  const isTimezoneValid = () => {
    return FormValidation.emptyCheck(timezone);
  };

  const isReasonFormValid = () => {
    return FormValidation.emptyCheck(reason);
  };
  const isUniqueFormValid = () => {
    return FormValidation.emptyCheck(unique);
  };
  const isNameFormValid = () => {
    return FormValidation.emptyCheck(name);
  };
  const isPositionFormValid = () => {
    return FormValidation.emptyCheck(position);
  };
  const isEmailFormValid = () => {
    return (
      FormValidation.emptyCheck(email) && FormValidation.emailFormatCheck(email)
    );
  };
  const isPhoneFormValid = () => {
    return (
      FormValidation.emptyCheck(phone) && FormValidation.phoneNumberCheck(phone)
    );
  };

  const getUserTimezoneOffset = () => {
    return new Date().getTimezoneOffset();
  };

  useEffect(() => {
    async function fetchData() {
      // fetch the data and trigger repaint
      const userLocationData = await getUserLocationData();
      setUserLocationData(userLocationData);
      // populate the form initial value with the location data

      // step 1 - search the timezone string
      const locationBasedTimezoneResult = lookupTimezone(
        userLocationData.timezone
      );
      setTimezone(locationBasedTimezoneResult.rawFormat);
    }
    fetchData();
  }, []);

  const submitForm = () => {
    setFormState(FormState.SUBMITTING);
    console.log("submitting form...");

    const formDataBundle = {
      "Primary Contact Name:": name,
      "Primary Contact Email:": email,
      "Primary Contact Phone Number:": phone,
      "Primary Contact Position:": position,
      "University Time Zone (GMT):": timezone,
      "University:": university,
      "Why does your university want to compete at CaseIT 2021?": reason,
      "What makes your university unique?": unique,
      Timestamp: generateCurrentDateTimeString(),
      Token: "submitted via caseit.org",
    };

    const serializedForm = encode({
      "form-name": NETLIFY_FORM_NAME,
      ...formDataBundle,
    });
    console.log(serializedForm);

    // send a request to netlify server
    // https://www.netlify.com/blog/2017/07/20/how-to-integrate-netlifys-form-handling-in-a-react-app/#form-handling-with-a-stateful-react-form
    fetch("https://caseit.org/", {
      method: "POST",
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      body: serializedForm,
    })
      .then(() => {
        setFormState(FormState.SUBMITTED);
      })
      .catch((e) => {
        setFormState(FormState.ERROR);
      });
  };

  const renderApplicationForm = () => {
    return (
      <>
        {/* Welcoming Page */}
        <Route exact path="/">
          <div className="modal__content modal__content--medium">
            <h1 className="h3">CaseIT 2021 Applications</h1>
            <p>
              To apply for CaseIT 2021, our applicants will have to answer the
              following questions:
            </p>
            <ul>
              <li>Why does your university want to compete at CaseIT 2020?</li>
              <li>
                What makes your university unique compared to other
                universities?
              </li>
            </ul>

            <p style={{ whiteSpace: "pre-wrap" }}>
              Completion of this application does not guarantee a spot in the
              competition. Applicants{" "}
              <strong>
                will be notified of application status within 2 weeks
              </strong>{" "}
              of submitting an application. For any questions, do not hesitate
              to email us at{" "}
              <a href="mailto:caseit@sfu.ca" target="blank">
                caseit@sfu.ca
              </a>
              .
            </p>

            <Link to="university">
              <Button label="Let's Begin" />
            </Link>
          </div>
        </Route>

        {/* University Page */}
        <Page
          name="university"
          prevPage=""
          nextPage="reason-to-compete"
          formReady={isUniversityFormValid()}
        >
          <div className="modal__content modal__content--narrow">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">I am applying on behalf of...</p>

            <AutocompleteField
              label="Name of University"
              placeholder="University Name"
              required={true}
              onChange={setUniversity}
              value={university}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter your university name";
              }}
              suggestions={UniversityList}
              focus
            />
          </div>
        </Page>

        {/* Reason to compete */}
        <Page
          name="reason-to-compete"
          prevPage="university"
          nextPage="university-unique"
          formReady={isReasonFormValid()}
        >
          <div className="modal__content modal__content--wide">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              Question 1: Why does <strong>{university}</strong> want to compete
              at CaseIT 2021?
            </p>
          </div>
          <Textarea
            placeholder={`State ${university}'s reason to compete...`}
            onChange={setReason}
            value={reason}
            focus
          />
        </Page>

        {/* University uniqueness */}
        <Page
          name="university-unique"
          prevPage="reason-to-compete"
          nextPage="timezone"
          formReady={isUniqueFormValid()}
        >
          <div className="modal__content modal__content--wide">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              Question 2: What makes <strong>{university}</strong> unique
              compared to other universities?
            </p>
          </div>
          <Textarea
            placeholder={`State ${university}'s unique competitive edge...`}
            onChange={setUnique}
            value={unique}
            focus
          />
        </Page>

        {/* University Timezone */}
        <Page
          name="timezone"
          prevPage="university-unique"
          nextPage="name"
          formReady={isTimezoneValid()}
        >
          <div className="modal__content modal__content--narrow">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              Please select the timezone at which <strong>{university}</strong>{" "}
              is located
            </p>

            <AutocompleteField
              focus
              label="Timezone"
              placeholder="Timezone"
              required={true}
              onChange={setTimezone}
              value={timezone}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter the university's timezone";
              }}
              suggestionsScrollable={true}
              suggestWhenEmpty={true}
              suggestions={timezones}
            />
          </div>
        </Page>

        {/* Name Page */}
        <Page
          name="name"
          prevPage="timezone"
          nextPage="position"
          formReady={isNameFormValid()}
        >
          <div className="modal__content modal__content--narrow">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              As the primary contact for <strong>{university}</strong>, my name
              is
            </p>
            <Textfield
              label="Full Name"
              placeholder="John Doe"
              required={true}
              onChange={setName}
              value={name}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter your full name";
                return;
              }}
              focus
            />
          </div>
        </Page>

        {/* Person's position */}
        <Page
          name="position"
          prevPage="name"
          nextPage="contact-info"
          formReady={isPositionFormValid()}
        >
          <div className="modal__content modal__content--narrow">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              My position in <strong>{university}</strong> is
            </p>
            <Textfield
              label="Position"
              placeholder="faculty member"
              required={true}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter your position";
                return;
              }}
              onChange={setPosition}
              value={position}
              focus
            />
          </div>
        </Page>

        {/* Person's contact */}
        <Page
          name="contact-info"
          prevPage="position"
          nextPage="confirmation"
          formReady={isEmailFormValid() && isPhoneFormValid()}
        >
          <div className="modal__content modal__content--narrow">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <p className="lead">
              My contact information for future communications
            </p>
            <Textfield
              focus
              label="Phone Number"
              type="tel"
              required={true}
              placeholder="+1 (604) 123 4567"
              onChange={setPhone}
              value={phone}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter your phone number";
                if (!FormValidation.phoneNumberCheck(val))
                  return "Invalid phone number";
                return;
              }}
            />
            <Textfield
              label="Email"
              type="email"
              placeholder="example@sfu.ca"
              required={true}
              onChange={setEmail}
              value={email}
              validateInput={(val) => {
                if (!FormValidation.emptyCheck(val))
                  return "Please enter your email address";
                if (!FormValidation.emailFormatCheck(val))
                  return "Invalid email address";
                return;
              }}
            />
          </div>
        </Page>

        <Route exact path="/confirmation">
          <div className="modal__content  modal__content--wide">
            <StepIndicator
              current={countStepAndIncrement()}
              total={TOTAL_STEPS}
            />
            <h1 className="h2 summery-header">Application Confirmation</h1>

            <div className="question-prompt">
              University{" "}
              <Link to="university" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">{university}</div>

            <div className="question-prompt">
              University Timezone{" "}
              <Link to="timezone" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">{timezone}</div>

            <div className="question-prompt">
              Question 1: Why does your university ({university}) want to
              compete at CaseIT 2021?{" "}
              <Link to="reason-to-compete" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">
              {reason.split("\n").map((i, key) => {
                return <div key={key}>{i}</div>;
              })}
            </div>

            <div className="question-prompt">
              Question 2: What makes your university ({university}) unique
              compared to other universities?{" "}
              <Link to="university-unique" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">
              {unique.split("\n").map((i, key) => {
                return <div key={key}>{i}</div>;
              })}
            </div>

            <div className="question-prompt">
              University Contact Name{" "}
              <Link to="name" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">{name}</div>

            <div className="question-prompt">
              University Contact Position{" "}
              <Link to="position" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">{position}</div>

            <div className="question-prompt">
              University Contact Information{" "}
              <Link to="contact-info" className="link-reset">
                Edit
              </Link>
            </div>
            <div className="question-answer">
              <div>Phone: {phone}</div>
              <div>Email: {email}</div>
            </div>
          </div>
          <div className="form-controls modal__content">
            <Link to="contact-info" className="link-reset">
              <Button label="back" type="secondary" />
            </Link>
            <Button label="Submit" type="primary" onClick={submitForm} />
          </div>
        </Route>
      </>
    );
  };

  const renderSubmittingInterface = () => {
    return (
      <div className="modal__content">
        <p>submitting...</p>
      </div>
    );
  };

  const rednerSubmittedInterface = () => {
    return (
      <div className="modal__content">
        <h1 className="h2 summery-header">Application Submitted!</h1>
        <p>We will send you a confirmation email very soon.</p>
        <a href="https://caseit.org/">back to home</a>
      </div>
    );
  };

  const renderErrorInterface = () => {
    return (
      <div className="modal__content">
        <h1 className="h2 summery-header">Ouch :(</h1>
        <p>
          Looks like an error occured during submission, please use the
          following link to try again or contact{" "}
          <a href="mailto:caseit@sfu.ca" target="blank">
            caseit@sfu.ca
          </a>
        </p>
        <a href="/registration-form" className="button-primary">
          Retry
        </a>
      </div>
    );
  };

  return (
    <div>
      <Modal>
        <BrowserRouter basename={REGISTATION_FORM_BASE_URL}>
          {formState === FormState.FILLING && renderApplicationForm()}
          {formState === FormState.SUBMITTING && renderSubmittingInterface()}
          {formState === FormState.SUBMITTED && rednerSubmittedInterface()}
          {formState === FormState.ERROR && renderErrorInterface()}
        </BrowserRouter>
      </Modal>
    </div>
  );
}

// short function for encoding the form data
const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};

const generateCurrentDateTimeString = () => {
  const today = new Date();
  const date =
    today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
  const time =
    today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
  const dateTime = date + " " + time;
  return dateTime;
};

export default App;
