import React, { useState, useRef } from "react";
import PropTypes from "prop-types";
import Textfield from "../textfield";
import "./AutocompleteField.scss";

function AutocompleteField(props) {
  const suggestionsScrollable = props.suggestionsScrollable || false;
  const maxResult = (() => {
    if (suggestionsScrollable) return props.suggestions.length;
    if (props.maxResult) return props.maxResult;
    return 5;
  })(); // maximun number of result get from the list
  const suggestWhenEmpty = props.suggestWhenEmpty || false;
  const [showField, setShowField] = useState(false);
  const [userInput, setUserInput] = useState(props.value);
  const [selectedIndex, setSelectedIndex] = useState(0);

  // trigger auto complete box showing
  const focusHandler = (e) => (suggestWhenEmpty ? setShowField(true) : false);
  const blurHandler = (e) => setShowField(false);

  const filteredSuggestion = useRef();
  const validateFunctionRef = useRef(); //

  const triggerOnChange = (value) => {
    props.onChange(value);

    validateFunctionRef.current(value);
  };

  const changeHandler = (val) => {
    setSelectedIndex(0);
    setUserInput(val);
    triggerOnChange(val);
  };

  const commitSelection = (e) => {
    // the user is selecting nothing
    if (selectedIndex === 0) {
      setShowField(false);
      return;
    }
    const selectedWords = filteredSuggestion.current[selectedIndex - 1];
    setUserInput(selectedWords);
    setShowField(false);
    triggerOnChange(selectedWords);
  };

  const suggestionItemMouseDownHandler = (e) => {
    commitSelection();
  };

  // filter suggestions function
  const filterSuggestion = () =>
    props.suggestions
      .filter((val) => {
        // search within the suggestion that if filter exist
        return val.toLowerCase().indexOf(userInput.toLowerCase()) > -1;
      })
      .slice(0, maxResult);

  // update the filter list
  filteredSuggestion.current = filterSuggestion();

  // if the item we selecting is over the filter list length
  if (selectedIndex > filteredSuggestion.current.length)
    setSelectedIndex(filteredSuggestion.current.length - 1);

  // handle keyboard navigation
  const handleKeyDown = (e) => {
    // set the state to show field automatically everytime key down
    setShowField(true);
    // check for selected list
    if (e.key === "Escape") {
      // user can use escale to dismiss
      setShowField(false);
    }

    if (e.key === "ArrowDown") {
      if (selectedIndex < maxResult) setSelectedIndex(selectedIndex + 1);
      e.stopPropagation();
    }
    if (e.key === "ArrowUp") {
      if (selectedIndex > 0) setSelectedIndex(selectedIndex - 1);
      e.stopPropagation();
    }

    // when the enter key is hit when selecting field...
    if (e.key === "Enter" && showField) {
      e.stopPropagation();
      commitSelection();
    }
  };

  const listClassName = () => {
    let compileClassName = showField
      ? "autocomplete-field__list"
      : "autocomplete-field__list autocomplete-field__list--hidden";

    compileClassName += suggestionsScrollable
      ? " autocomplete-field__list--scrollable"
      : "";

    return compileClassName;
  };

  return (
    <div className="autocomplete-field" onKeyDown={handleKeyDown}>
      <Textfield
        {...props}
        value={userInput}
        onFocus={focusHandler}
        onBlur={blurHandler}
        onChange={changeHandler}
        triggerValidateFunctionRef={validateFunctionRef}
      />
      <ul className={listClassName()}>
        {filteredSuggestion.current.map((val, index) => {
          const currentSelectedIndex = index + 1;
          return (
            <li
              key={index}
              className={
                selectedIndex === currentSelectedIndex ? "highlighted" : ""
              }
              onMouseDown={suggestionItemMouseDownHandler}
              onMouseOver={() => setSelectedIndex(currentSelectedIndex)}
            >
              {val}
            </li>
          );
        })}
      </ul>
    </div>
  );
}

AutocompleteField.propTypes = {
  suggestions: PropTypes.array,
  suggestWhenEmpty: PropTypes.bool,
};

export default AutocompleteField;
