import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Route, Redirect, Link } from "react-router-dom";
import Button from "../button";

// "onPageDataChange"
// "onPageStateChange"

function Page(props) {
  const [triggerNextPage, setTriggerNextPage] = useState(false);

  const prevPageRender = () => {
    if (props.prevPage === undefined) return null;

    return (
      <Link to={`/${props.prevPage}`} className="link-reset">
        <Button label="Back" type="secondary" disabled={false} />
      </Link>
    );
  };

  const nextPageRender = () => {
    if (props.nextPage === undefined) return null;

    // render a disabled button if the form is not ready
    if (!props.formReady) return <Button label="Next" disabled={true} />;
    return (
      <Link to={`/${props.nextPage}`} className="link-reset">
        <Button label="Next" disabled={false} />
      </Link>
    );
  };

  const submitRedirectRender = () => {
    return <Redirect push to={`/${props.nextPage}`} />;
  };

  // dirty way to trigger submit
  const submitHandler = (e) => {
    // when user hit enter
    if (e.keyCode == 13) {
      // if the form content is ready to move on, and also there is a next page defined
      if (props.nextPage !== undefined && props.formReady) {
        setTriggerNextPage(true);
      }
    }
  };

  // clean up after redirect
  useEffect(() => {
    setTriggerNextPage(false);
  }, [triggerNextPage]);

  if (triggerNextPage) {
    // setTriggerNextPage(false);
    return submitRedirectRender();
  }

  return (
    <Route exact path={`/${props.name}`}>
      {/* container for recording keystrokes */}
      <div className="page-container" onKeyDown={submitHandler}>
        {props.children}
        <div className="modal__content form-controls">
          {prevPageRender()}
          {nextPageRender()}
        </div>
      </div>
    </Route>
  );
}

Page.propTypes = {
  nextPage: PropTypes.string, // the route for next page
  prevPage: PropTypes.string, // the route for prev page
  name: PropTypes.string, // the route for current apge
  formReady: PropTypes.bool, // tell the component can it switch to other pageslkop09
};

export default Page;
